// eslint-disable-next-line import/no-unresolved
import { pipe } from 'rxjs';
import { filter, map, pairwise, scan } from 'rxjs/operators';
import { AXES, Direction, getDirection, isOnGap, } from '../../utilities/axis';
export function filterByAttributeAndGapOperator(k, gap = AXES, ignoreWhen) {
    return pipe(map((scrollObserver) => {
        return {
            current: scrollObserver,
            last: scrollObserver,
        };
    }), scan((acc, curr) => {
        const axes = acc.current[k];
        const lastAxes = acc.last[k];
        const onGap = isOnGap({
            axes,
            gap,
            lastAxes,
        });
        return {
            current: curr.current,
            last: onGap ? acc.last : curr.current,
        };
    }), filter(({ current, last }, index) => {
        const axes = current[k];
        const lastAxes = last[k];
        const firstEvent = !index;
        const isToIgnore = current[ignoreWhen.key] === ignoreWhen.value;
        if (firstEvent || isToIgnore)
            return true;
        return !isOnGap({
            axes,
            gap,
            lastAxes,
        });
    }), map((scrollObserver) => {
        return scrollObserver.current;
    }));
}
export function putRelativeAxesOperator(k, relativeK, startK, restartWhen) {
    return pipe(scan((acc, curr, index) => {
        const firstRun = index === 1;
        const toRestart = curr[restartWhen.key] === restartWhen.value;
        let startAxes = acc[startK];
        const currAxes = curr[k];
        if (firstRun || toRestart) {
            startAxes = currAxes;
        }
        const relativeX = currAxes.x - startAxes.x;
        const relativeY = currAxes.y - startAxes.y;
        return Object.assign(Object.assign({}, curr), { [startK]: startAxes, [relativeK]: {
                x: relativeX,
                y: relativeY,
            } });
    }));
}
export function putAxesBreakpointOperator(gap = AXES, k, relativeBreakpointK) {
    return pipe(map((value) => {
        const axes = value[k];
        const x = Math.floor(axes.x / gap.x || 1) - 1;
        const y = Math.floor(axes.y / gap.y || 1) - 1;
        return Object.assign(Object.assign({}, value), { [relativeBreakpointK]: {
                x,
                y,
            } });
    }));
}
export function putDirectionOperator(k, directionK, ignoreWhen) {
    return pipe(pairwise(), map(([last, curr]) => {
        const lastAxes = last[k];
        const currAxes = curr[k];
        let direction = last[directionK] || Direction.NONE;
        const isToIgnore = curr[ignoreWhen.key] === ignoreWhen.value;
        if (!isToIgnore) {
            direction = getDirection({
                axes: currAxes,
                lastAxes,
            });
        }
        return Object.assign(Object.assign({}, curr), { [directionK]: direction });
    }));
}
