import './sticky-header.scss';
import { getHeight, translate } from '../../utilities/element';
import { Scroll$ } from '../../api/scroll';
const isNodeJS = typeof window === 'undefined';
export default function StickyHeader({ el = !isNodeJS
    ? document.querySelector('[data-ovo-sticky-header]')
    : undefined, }) {
    if (!el)
        return;
    const htmlEl = el;
    function handleDocumentScroll({ relativeAxes: { y: position = 0 }, }) {
        translate({ el: htmlEl, position });
    }
    function bindEvents() {
        if (!el)
            return;
        Scroll$({
            maxRelative: {
                top: 0,
                bottom: getHeight(htmlEl),
                left: 0,
                right: 0,
            },
        }).subscribe(handleDocumentScroll);
    }
    bindEvents();
}
function autoStart() {
    if (typeof document === 'undefined')
        return;
    const flag = document.querySelector('[data-ovo-sticky-header][data-ovo-auto]');
    if (!flag)
        return;
    StickyHeader({});
}
autoStart();
